              :counter="16"
              type="password"
              :error-messages="errors"
              label=""
              required
              maxlength="16"
            ></v-text-field>
          </ValidationProvider>

          <ValidationProvider
            name="confirm"
            rules="required|passswordConfirm:@password"
            v-slot="{ errors }"
          >
            <v-text-field
              v-model="confirmation"
              :counter="password.length"
              type="password"
              :error-messages="errors"
