                <v-btn block color="primary" text @click="dialog = false, checkbox=true">.</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <ValidationProvider v-slot="{ errors }" rules="agree" name="checkbox">
            <v-checkbox
              v-model="checkbox"
              :error-messages="errors"
              label=""
              type="checkbox"
              required
              width="100px"
            ></v-checkbox>
          </ValidationProvider>

          <v-btn color="primary" :disabled="!valid" class="mr-4" @click="submit"></v-btn>
        </v-form>
      </ValidationObserver>
      <!--  -->
      <v-alert
        v-model="alert"
        type="error"
        dense
        outlined
        dismissible
        class="ma-5"
      >{{ errorsCommonPwd }}</v-alert>
    </v-row>
  </v-container>
