<script>
import { required, email, max, min } from "vee-validate/dist/rules";
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode
} from "vee-validate";
import http from "@/util/http-common";
setInteractionMode("eager");

extend("required", {
  ...required,
  message: " ."
});

extend("min", {
  ...min,
  message: "{length}   "
});

extend("max", {
  ...max,
  message: "{length}   "
});

extend("email", {
  ...email,
  message: "  "
});

extend("passswordConfirm", {
  params: ["target"],
  validate(value, { target }) {
    return value === target;
  },
  message: "  "
});
extend("agree", {
  ...required,
  message: " "
});

export default {
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data: () => ({
    name: "",
    email: "",
    password: "",
    confirmation: "",
    checkbox: null,
    errorsEmail: [],
    errorsName: [],
    valid: false,
    dialog: false,
    errorsCommonPwd: "  .",
    alert: false
  }),
  watch: {
    password() {
      this.alert = false;
    },
    email() {
      let valid = false;

      if (this.email.length === 0) {
        valid = false;
        this.errorsEmail = valid ? [] : [" ."];
      } else {
        let re = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;

        if (!re.test(this.email)) {
          valid = false;
          this.errorsEmail = valid ? [] : ["  "];
        } else {
          valid = true;
          const fd = new FormData();
          fd.append("email", this.email);
          http.post("/accounts/duplicated/email/", fd).then(res => {
            if (res.data.message === "  .") {
              valid = false;
            }
            this.errorsEmail = valid ? [] : ["  ."];
          });
        }
      }
    },
    name() {
      let valid = false;

      if (this.name.length === 0) {
        valid = false;
        this.errorsName = valid ? [] : [" ."];
      } else if (this.name.length > 10) {
        valid = false;
        this.errorsName = valid ? [] : ["Max 10 characters"];
      } else {
        var re = /^([\w--@/./+/-]*)$/;
        if (!re.test(this.name)) {
          valid - false;
          this.errorsName = valid
            ? []
            : [" , , @ . + - _  ."];
        } else {
          valid = true;
          const fd = new FormData();
          fd.append("username", this.name);
          http.post("/accounts/duplicated/username/", fd).then(res => {
            if (res.data.message === "  .") {
              valid = false;
            }
            this.errorsName = valid ? [] : ["  ."];
          });
        }
      }
    }
  },
  methods: {
    async submit() {
      const isValid = await this.$refs.observer.validate();
      if (isValid) {
        http
          .post("/signup/", {
            email: this.email,
            username: this.name,
            password1: this.password,
            password2: this.confirmation
          })
          .then(response => {
            console.log(response);
            let token = response.data.key;
            localStorage.setItem("token", token);

            this.$router.push({
              name: "taste",
              params: { username: this.name }
            });
          })
          .catch(error => {
            var message = error.message;

            if (message.includes("status code 400")) {
              this.alert = true;
              this.valid = false;
            }
          });
      }
    }
  }
};
</script>
